import stocks from '../stocksData'

export default {
  state: {
    fontStyle: {
      fontFamily: 'Indie Flower',
      color: 'red'
    },
    stocks
    // randomStock: this.stock.price
  },
  mutations: {
    setStocks: (state, stocks) => {
      state.stocks = stocks
    },
    rndStocks: (state) => {
      for (let stock of state.stocks) {
        stock.price = Math.round(stock.price * (1 + Math.random() - 0.4))
      }
    }

  },
  actions: {
    randomizeStock: ({ commit }) => {
      commit('rndStocks')
    },
    initStocks: ({ commit }) => {
      commit('setStocks', stocks)
    },
    alterFonds: ({ commit }, payload) => {
      commit('mutateFonds', payload)
    }

  },
  getters: {
    getFunds: state => {
      return state.myFunds.funds
    },

    myStocks: state => {
      return state.stocks
    },
    getRandomPrice: state => {
      return state.randomStock
    }
  }

}
