<template>

      <div class="stocks ">
         <div class="form ">
            <div class="form__group stocks__control">

<label for="apple" class="form__label label-control">
                <p class="label-control__head">{{stock.name}}</p>
                <p class="heading-4-white label-control__text"> Buy Price: &nbsp; <span> {{stock.price}}   </span>

                  <span  v-if="cheaperBuy" :class="{greenClass: cheaperBuy}" > - {{displayprice}} <i :class="{greenClass: cheaperBuy}"  class="fas fa-sort-down arrow"></i></span>

                  <span v-else  :class="{redClass: !cheaperBuy}" > + {{displayprice}} <i :class="{redClass: !cheaperBuy}" class="fas fa-sort-up arrow" ></i> </span>
                   </p>



                </label>



              <div class="stocks__control__form">
<input v-model.number="quantity" name="apple" class="form__control stock-input " type="number" placeholder="Quantity" :class="{danger: insufficientFunds}">
<div class="stockChoice">
<a :class="{disabled: insufficientFunds || quantity <= 0 || !Number.isInteger }" @click.prevent="buyStock"  class="special-btn special-btn-green special-btn-widest">{{insufficientFunds ? 'insufficientFunds' : 'Buy' }}</a>
<a  @click="endDay" class=" special-btn special-btn-red special-btn-widest">End Day</a>
</div>

              </div>

            </div>

          </div>






  </div>
</template>

<script>
import { mapActions, mapGetters, mapState } from 'vuex';
export default {
  props: ['stock'],
data(){
  return{
    quantity: 0,
  disabled: false,
  cheaperBuy: true,
  changedPrice: '',
  displayprice: '',
  previousPrice: '',
  currentPrice: '',

  }
},


computed: {

  ...mapGetters({initialFunds: 'funds'}),

disableButton(){
  if(this.quantity * this.stock.price > this.initialFunds ||this.quantity <= 0 || !Number.isInteger ){
    this.disabled = true;
    console.log("error");
  }

},

insufficientFunds(){
  return this.quantity * this.stock.price > this.initialFunds
}

},



methods: {
    ...mapActions(
       { changeFunds: 'alterFonds',randomizeStock: 'randomizeStock'},
      // { order: 'buyStock'}
      ),

      endDay(){
this.randomizeStock();
this.showItem = false;
},
monitorStocks(){



setTimeout(() => {

  this.changedPrice = '';
    this.previousPrice = this.currentPrice;
   console.log('Current Price is' + this.previousPrice)
}, 500, this.watchPrice());



  },




watchPrice(){
if(this.previousPrice > this.currentPrice){
this.cheaperBuy = true
return this.changedPrice = this.previousPrice - this.currentPrice

  console.log('Price went down')

}else if(this.previousPrice < this.currentPrice){
  this.cheaperBuy = false
return this.changedPrice = this.currentPrice - this.previousPrice

 console.log('Price went up')
  }

},
  buyStock(){

    const order = {
      stockId: this.stock.id,
      quantity: this.quantity,
      stockPrice: this.stock.price,
      stockName: this.stock.name,


    }
    this.$store.dispatch('buyStock', order)
    console.log(order)


    this.quantity = 0;
  }


  },

mounted(){
this.previousPrice = this.stock.price

},
  updated(){
this.currentPrice = this.stock.price
this.monitorStocks();
this.displayprice = this.changedPrice;

  }
}
</script>
