<template>

    <div class="modal-layout">
        <transition name="fade">
        <div  class="modal" >
            <div class="modal__title">
                <slot name="title">
                Default Title
                </slot>
            </div>
            <div class="modal__desc">
                <slot name="description">
                    Default Desc
                </slot>
            </div>
            <div class="modal__list">
                <slot name = "listHead"></slot>
                <slot name="list">
                    Default Desc
                </slot>
            </div>
            <div class="modal__footer">
                <slot name="footer">
                    <a class = "btn btn-blue btn-wide " @click="goToApp">Go To App</a>
                    <a class = "btn btn-white btn-wide" @click="close">Close</a>
                </slot>
            </div>
        </div>
        </transition>
    </div>

</template>

<script>
export default {
    name: 'modal',

    methods: {
        close(){
            this.$emit('close');
        },
        goToApp(){
            this.$emit('goToApp');
        }
    }
}
</script>
