<template>



      <div class="stocks ">
         <div class="form ">
            <div class="form__group stocks__control">
              <label for="apple" class="form__label label-control">
                <p class="label-control__head">{{stock.name}}</p>
                <p class="heading-4-white label-control__text"> Stock Quantity: {{stock.quantity}} | Price: &nbsp; <span> {{stock.price}}   </span>

                  <span  v-if="higherSale" :class="{greenClass: higherSale}" > + {{displayprice}} <i :class="{greenClass: higherSale}"  class="fas fa-sort-up arrow"></i></span>

                  <span v-else  :class="{redClass: !higherSale}" > - {{displayprice}} <i :class="{redClass: !higherSale}" class="fas fa-sort-down arrow" ></i> </span>
                   </p>
            <p  class="heading-4-white">Profit: <span :class="{greenClass: showProfit, redClass: !showProfit}" >{{profit}}</span></p>


                </label>
              <div class="stocks__control__form">
<input v-model.number="quantity" name="apple" class="form__control stock-input "
type="number"
placeholder="Quantity"
:class="{danger: insufficientQuantity}">


<div class="stockChoice">
<a @click="sellStock" :class="{disabled: insufficientQuantity || quantity <= 0 || !Number.isInteger }"   class="special-btn special-btn-green special-btn-widest">{{insufficientQuantity ? 'Not enough Stocks' : 'Sell' }}</a>
<a  @click="endDay" class=" special-btn special-btn-red special-btn-widest">End Day</a>
</div>
              </div>

            </div>

          </div>






  </div>
</template>

<script>
import { mapActions, mapGetters, mapState } from 'vuex';
export default {
  props: ['stock'],
data(){
  return{
    quantity: 0,
changedPrice: '',
displayprice: '',
  previousPrice: '',
  currentPrice: '',
  higherSale: '',
  showProfit: true,
  profit: '',
  }
},

computed: {
insufficientQuantity(){
  return this.quantity > this.stock.quantity
},



},

methods: {

checkQuantity(){
  if(this.quantity <= this.stock.quantity){
   this.profit =  this.quantity * this.stock.price
   this.showProfit = true;
  }else if(this.quantity > this.stock.quantity){
this.profit = 'Not Enough Stocks'
this.showProfit = false;
  }
},
    ...mapActions(
      { changeFunds: 'alterFonds',randomizeStock: 'randomizeStock'},

      ),

sellStock(){
  const order = {
    stockId: this.stock.id,
      quantity: this.quantity,
      stockPrice: this.stock.price,
      stockName: this.stock.name,

  };
  this.$store.dispatch('sellStock', order)

  this.quantity = 0;
},
 endDay(){
this.randomizeStock();
this.showItem = false;
},
monitorStocks(){

setTimeout(() => {
  this.changedPrice = '';
    this.previousPrice = this.currentPrice;
   console.log('Current Price is' + this.previousPrice)
}, 500, this.watchPrice());



  },
watchPrice(){
if(this.previousPrice > this.currentPrice){
this.higherSale = false
return this.changedPrice = this.previousPrice - this.currentPrice

  console.log('Price went down')

}else if(this.previousPrice < this.currentPrice){
  this.higherSale = true
return this.changedPrice = this.currentPrice - this.previousPrice

 console.log('Price went up')
  }

},

  },


mounted(){
this.previousPrice = this.stock.price

},
  updated(){
this.currentPrice = this.stock.price
this.monitorStocks();
this.displayprice = this.changedPrice;
this.checkQuantity()
  }

}
</script>
