<template>

<section  v-scroll-reveal.reset class ='skill-section U-margin-md'>
          <transition name="slide" appear>
<div class="skills ">


        <h3 class="skills-description heading-3 ">skills</h3>


    <ul class="skills-list">


            <div class="progressContainer">
                <div class="header">
                <h4 class="heading-4">HTML</h4>
                </div>
                 <div class="progressBox skillProgress ">
                <div class = "progressBox__outerBox">
<div v-on="changePercent" appear class = "progressBox__innerBox" :style="{width: htmlRating + '%'}">{{htmlRating + '%'}}</div>
                </div>

            </div>
            </div>

            <div class="progressContainer">
                <div class="header">
                <h4 class="heading-4">CSS</h4>
                </div>
                 <div class="progressBox skillProgress">
                <div class = "progressBox__outerBox">
<div v-on="changePercent" appear class = "progressBox__innerBox" :style="{width: cssRating + '%'}">{{cssRating + '%'}}</div>
                </div>

            </div>
            </div>

            <div class="progressContainer">
                <div class="header">
                <h4 class="heading-4">CSS-GRID</h4>
                </div>
                 <div class="progressBox skillProgress">
                <div class = "progressBox__outerBox">
<div v-on="changePercent" appear class = "progressBox__innerBox" :style="{width: cssGridRating + '%'}">{{cssGridRating + '%'}}</div>
                </div>

            </div>
            </div>

            <div class="progressContainer">
                <div class="header">
                <h4 class="heading-4">BEM</h4>
                </div>
                 <div class="progressBox skillProgress">
                <div class = "progressBox__outerBox">
<div v-on="changePercent" appear class = "progressBox__innerBox" :style="{width: bemRating + '%'}">{{bemRating + '%'}}</div>
                </div>

            </div>
            </div>

            <div class="progressContainer">
                <div class="header">
                <h4 class="heading-4">SASS</h4>
                </div>
                 <div class="progressBox skillProgress">
                <div class = "progressBox__outerBox">
<div v-on="changePercent" appear class = "progressBox__innerBox" :style="{width: sassRating + '%'}">{{sassRating + '%'}}</div>
                </div>

            </div>
            </div>

            <div class="progressContainer">
                <div class="header">
                <h4 class="heading-4">JAVASCRIPT</h4>
                </div>
                 <div class="progressBox skillProgress">
                <div class = "progressBox__outerBox">
<div v-on="changePercent" appear class = "progressBox__innerBox" :style="{width: javascriptRating + '%' }">{{javascriptRating + '%'}}</div>
                </div>

            </div>
            </div>

            <div class="progressContainer">
                <div class="header">
                <h4 class="heading-4">VUE.JS</h4>
                </div>
                 <div class="progressBox skillProgress">
                <div class = "progressBox__outerBox">
<div v-on="changePercent" appear class = "progressBox__innerBox" :style="{width: vueRating + '%' }">{{vueRating + '%'}}</div>
                </div>

            </div>
            </div>

            <div class="progressContainer">
                <div class="header">
                <h4 class="heading-4">Node.JS</h4>
                </div>
                 <div class="progressBox skillProgress">
                <div class = "progressBox__outerBox">
<div v-on="changePercent" appear class = "progressBox__innerBox" :style="{width: nodeRating + '%' }">{{nodeRating + '%'}}</div>
                </div>

            </div>
            </div>

            <div class="progressContainer">
                <div class="header">
                <h4 class="heading-4">Mongo.DB</h4>
                </div>
                 <div class="progressBox skillProgress ">
                <div class = "progressBox__outerBox">
<div v-on="changePercent" appear class = "progressBox__innerBox" :class="{redBackground, colorCode}" :style="{width: mongoRating + '%', background: orangeBackground}">{{mongoRating + '%'}}</div>
                </div>

            </div>
            </div>



    </ul>

</div>
          </transition>
         </section>

</template>

<script>

export default {

    components: {
    },

data(){
    return{
        load: true,
        htmlRating: 0,
        cssRating : 0,
        cssGridRating: 0,
        bemRating: 0,
        sassRating :0,
        javascriptRating : 0,
        vueRating : 0,
        nodeRating : 0,
        mongoRating: 0,

        colorCode: true,
        redBackground: "red !important",
        orangeBackground: "orange !important"

    }

},
methods:{
    changeColor(width){
            if(width < 50 + '%'){
                backgroundColor = this.redBackground;
            }else if((width > 50 + '%') && (width < 75 + '%')){
                backgroundColor = this.orangeBackground;
            }
    },
    changePercent(){
        const vm = this;

        setInterval(() => {

            if(this.htmlRating < 90){
                 this.htmlRating += 5;
            }
        }, 50);

        setInterval(() => {
            if(this.cssRating < 95){
              this.cssRating += 5;
            }
        }, 50);
        setInterval(() => {
            if(this.cssGridRating < 95){
              this.cssGridRating += 5;
            }
        }, 50);
        setInterval(() => {
            if(this.bemRating < 95){
              this.bemRating += 5;
            }
        }, 50);
        setInterval(() => {
            if(this.sassRating < 90){
              this.sassRating += 5;
            }
        }, 50);
        setInterval(() => {
            if(this.javascriptRating < 85){
              this.javascriptRating += 5;
            }
        }, 50);
        setInterval(() => {
            if(this.vueRating < 85){
              this.vueRating += 5;
            }
        }, 50);
        setInterval(() => {
            if(this.nodeRating < 85){
              this.nodeRating += 5;
            }
        }, 50);
        setInterval(() => {
            if(this.mongoRating < 65){
              this.mongoRating += 5;
            }
        }, 50);
    },

    setWidth(myWidth){
       return this.width = myWidth;
       this.myWidth = 0;
    }
},
created() {
    this.changePercent();
}

}

</script>
