  controlNav(){
    this.scrolled = window.scrollY > 0;

    if(this.$route.name === 'about' ){
      this.aboutRoute = true;
    }
  },
  handleScroll(){
    this.scrolled = window.scrollY > 0;

if (this.scrolled && this.$route.name === 'about'){
      this.aboutRoute = false
    }else if(!this.scrolled && this.$route.name === 'about'){
      this.aboutRoute = true;
    }
    if (!this.scrolled && !this.$route.name === 'about'){
      this.aboutRoute = false
    }
    if( this.showItem === true){
      this.aboutRoute = false
    }

  },


toggleNavDark(){
this.showItem = !this.showItem;
if(this.$route.name === 'about' ){
  this.aboutRoute = false;

}
window.scrollTo(0,0);
},
toggleNavTransparent(){

    this.showItem = !this.showItem;

this.aboutRoute = true;



}
},
