
export default {

  state: {
    myFunds: {
      funds: 10000
    },

    stocks: []
  },

  mutations: {
    'BUY_STOCK': (state, { stockId, quantity, stockPrice, stockName }) => {
      const record = state.stocks.find(element => element.id === stockId)
      if (record) {
        record.quantity += quantity
      } else {
        state.stocks.push({
          id: stockId,
          quantity: quantity,
          price: stockPrice,
          name: stockName
        })
      } state.myFunds.funds -= stockPrice * quantity
    },
    'SELL_STOCK': (state, { stockId, quantity, stockPrice }) => {
      const record = state.stocks.find(element => element.id === stockId)
      if (record.quantity > quantity) {
        record.quantity -= quantity
      } else {
        state.stocks.splice(state.stocks.indexOf(record), 1)
      }
      if (Math.max(state.myFunds.funds) < 10000) {
        state.myFunds.funds += stockPrice * quantity
      }
    },
    'SET_PORTFOLIO': (state, portfolio) => {
      state.myFunds.funds = portfolio.funds
      state.stocks = portfolio.stockPortfolio ? portfolio.stockPortfolio : []
    }

  },

  actions: {

    buyStock: ({ commit }, order) => {
      commit('BUY_STOCK', order)
    },
    sellStock: ({ commit }, order) => {
      commit('SELL_STOCK', order)
    }
  },
  getters: {
    stockPortfolio: (state, getters) => {
      return state.stocks.map(stock => {
        const record = getters.myStocks.find(element => element.id === stock.id)
        return {
          id: stock.id,
          quantity: stock.quantity,
          name: record.name,
          price: record.price
        }
      })
    },
    funds: (state) => {
      return state.myFunds.funds
    }
  }
}
