
export default {
  state: {
    counter: 0
  },

  getters: {
    tripleCounter: state => {
      return state.counter
    }
  },

  mutations: {
    mutateCounter: (state, payload) => {
      state.counter += payload
    }
  },

  actions: {
    increaseCounter: ({ commit }, payload) => {
      commit('mutateCounter', payload)
    }
  }
}
