<template>

<div class = " quote-grid U-margin-top-xl">



<!-- /////////////////////////////////////////////////// -->


    <div class="quote-header">
        <h3 class = "heading-3">Wonderful Quotes</h3>
        <br>
        <p class="heading-4-white">Wonderful quote allows you to add a quote of your choice </p>
        <p class="heading-4-white">Feel free to follow the instructions* </p>
        <br>
        <ul class="instructions">
        <li class="instructions__text"> <i class="fas fa-long-arrow-alt-right arrow "></i>  Be creative and enter a quote using the text area </li>
        <li class="instructions__text"> <i class="fas fa-long-arrow-alt-right arrow "></i>  Click Add Quotes to add your quote to the list: Maximum is 10</li>
          <li class="instructions__text"><i class="fas fa-long-arrow-alt-right arrow "></i> The progress bar will fill up by 1 for each quotes added</li>
          <li class="instructions__text"><i class="fas fa-long-arrow-alt-right arrow "></i> Once the progress bar is full, you will have to delete a quote by simply selecting any of your added quotes to remove it </li>
          <li class="instructions__text"><i class="fas fa-long-arrow-alt-right arrow "></i> Once removed you will be allowed to add more quotes</li>
          <li class="instructions__text"><i class="fas fa-long-arrow-alt-right arrow "></i> You also have the option of changing your fonts if you like</li>
          <li class="instructions__text"> Enjoy</li>
        </ul>
    </div>
    <div class="heading-3">Quotes Added</div>
        <div class="progressBox quoteProgress ">

            <div class="progressBox__outerBox">

                <div class = "progressBox__innerBox" :style="{width: Progresswidth + '%'}">
                   <p> {{counter + "/10"}}</p>

                </div>
            </div>


    </div>
    <div class="quote-box">
<div class="quote-box__heading">
   <h3 class = "heading-3" :style="alertColor" v-if="addQuote">{{controlQuote}}</h3>
   <h3 class = "heading-3" :style ="alertColor" v-else>{{controlQuote}}</h3>

</div>


            <form class="quote-box__box"   action="">
             <div class="form quote-box__box__input" >
<textarea :class="[isActive ? 'MacondoFont' : 'IndieFlower']" class = "form__control quote-control" v-model="quote" name="quoteEntry" id="" cols="40" rows="5"></textarea>
</div>

                <div class="form__group quote-box__box__btn">
                    <a @click.prevent="createNew" class=" special-btn special-btn-blue special-btn-widest">Add Quotes</a>
                     <a @click.prevent="changeFont" class=" special-btn special-btn-green special-btn-widest">Change Fonts</a>



                </div>
            </form>



    </div>

    <div :class="[isActive ? 'MacondoFont' : 'IndieFlower']" class="quote-list">
        <div class="quote-list__main">
            <h2 class="h2">{{lastQuote}}</h2></div>
        <div class="quote-list__all">
            <ul class = "QuoteUl">
                <li @click = "remove(index)" class = "QuoteUl__li" v-for="(myQuote, index) in quotes" :key="myQuote"  >
                    {{myQuote}}
                </li>
            </ul>
        </div>
    </div>




</div>





</template>

<script>
import { mapGetters } from 'vuex';


export default {


    data(){


     return{


  sendMail: [],
         gender: 'male',
         selectedPriority: 'High',
         priorities: ['High','Medium','Low'],
         currentQuote: 0,
         lastQuote: '',
         maxquote: 10,
         quote: '',
         controlQuote: 'Please enter a quote',
         isActive: true,
         quotes: [

         ],
         pStyle:{
             color: 'green'
         },
         Progresswidth: 0,
         counter: 0,
         addQuote: true,
         appwidth: 10,
         alertColor: {
             color: 'green'
         }
     }
    },

computed: {
...mapGetters({ defaultFont: 'getFont'}),
       },


    methods: {


changeFont(){
        this.isActive = !this.isActive
},
        createNew(){
            const vm = this;
           // let width = 10;
            if(vm.Progresswidth <= 90 && !(this.quote == ""))     {
                this.addQuote = true;
                this.alertColor.color = 'green';


                setTimeout(() => {
                    this.controlQuote = 'Please enter a quote'
                }, 1000,this.controlQuote = 'Quote Added');
vm.Progresswidth = vm.Progresswidth += this.appwidth;
this.counter += 1
 this.quotes.push(this.quote);
            this.lastQuote = this.quote;

            this.quote = '';
console.log('clicked');
            }else if(this.quote == ""){
                this.alertColor.color = 'red';
                setTimeout(() => {
                    this.controlQuote = 'Please enter a quote'
                     this.alertColor.color = 'green';
                }, 1000,
                this.controlQuote = 'Invalid Quote!'

                );
            }else if(vm.Progresswidth > 90){

                setTimeout(() => {
                    this.controlQuote = 'Please enter a quote'
                     this.alertColor.color = 'green';
                }, 1000,
                this.alertColor.color = 'red',
                this.controlQuote = 'Maximum Quote reached!: Delete a quote'

                );
            }


        },
        remove(index){
            const vm2 = this;
             //let width = 10;

           setTimeout(() => {
                 this.controlQuote = 'Please enter a quote'
                 this.alertColor.color = 'green'
           }, 1000,
           this.quotes.splice(index, 1),
              this.addQuote = false,
           this.alertColor.color = 'red',
           this.controlQuote = 'Quote Removed',
            vm2.Progresswidth = vm2.Progresswidth -= this.appwidth,
        vm2.counter -= 1

           );


        },
        // resetTimer(){
        //     setTimeout(() => {
        //          this.controlQuote = 'Please enter a wonderful quote'
        //          this.alertColor.color = 'green'
        //    }, 1000);
        // }

    },

}
</script>


