<template>
  <div class="stock-nav ">


    <router-link :to="{name: 'about'}" tag="li" class="returnHome"><a class="stock-item-1__link returnHome__Brand">Fs</a></router-link>

  <div @click="toggleNav" class="togglecontainer"><i class="fas fa-bars nav__toggle" >&nbsp;</i></div>

 <a href="" class="Stock-Brand">{{Brand ? BrandName : 'Funds: $' + initialFonds }}</a>


  <ul class="stock-item-1 heading-4-white" :class="{showStockNav: showItem}">
    <router-link @click.native="toggleNav" :to="{name: 'stockHome'}" tag="li" class="stock-item-1__list "  ><a class="stock-item-1__link" >Stock Trader</a></router-link>
    <router-link @click.native="toggleNav" :to="{name: 'stockPage'}" tag="li" class="stock-item-1__list " ><a class="stock-item-1__link" @click="closeTab()">Stocks</a></router-link>
<router-link @click.native="toggleNav" :to="{name: 'stockPortfolio'}" tag="li" class="stock-item-1__list " ><a class="stock-item-1__link" >Portfolio</a></router-link>


  </ul>

  <ul class="stock-item-2 heading-4-white" :class="{showStockNav: showItem}">

    <li class="stock-item-2__list special-btn special-btn-green special-btn-wide">
       <a @click="saveData" class="stock-item-2__link ">Save</a>
    </li>
    <li class="stock-item-2__list special-btn special-btn-orange special-btn-wide">
       <a @click="loadData" class="stock-item-2__link " >Load</a>
    </li>

    <li class="stock-item-2__list hideNav">
       <p class="">Funds: ${{initialFonds}} </p>
    </li>
  </ul>
</div>

</template>

<script>
import { mapGetters, mapActions } from 'vuex';
export default {
data(){
  return{
showItem: false,
Brand: true,
BrandName: 'Stock Trader',
windowWidth: window.innerWidth

  }
},


computed: {
    ...mapGetters(
      {initialFonds: 'funds'},
      ),


},
  methods: {
// ...mapActions (
//   ['randomizeStock']
// ),

...mapActions(
  {randomizeStock: 'randomizeStock',
    fetchData: 'loadData'
  }),
    toggleNav(){
      this.showItem = !this.showItem;

    },
closeTab(){
      this.showItem = false;
 console.log('closed');
    },
endDay(){
this.randomizeStock();
this.showItem = false;
},
...mapGetters(
          {funds: 'funds'},
          {stockPortfolio: 'stockPortfolio'},
          {stocks: 'myStocks'}
),
saveData(){
    const data = {

      funds: this.$store.getters.funds,
      stockPortfolio: this.$store.getters.stockPortfolio,
      stocks: this.$store.getters.myStocks

    };
    this.$http.put('https://stockproject-69d23.firebaseio.com/stocks.json',data);
    this.showItem = false;
},
loadData(){
this.fetchData();
this.showItem = false;
}

  },

mounted(){

     this.windowWidth = window.innerWidth
     if(this.windowWidth <= 768){
  this.Brand = false
  console.log(this.windowWidth);
}

}



}
</script>
