import Vue from 'vue'
import Router from 'vue-router'
import About from './pages/About.vue'
import Skills from './pages/Skills.vue'
import Portfolio from './pages/Portfolio.vue'
import Quotes from './pages/Quotes.vue'
import Stock from './pages/StockTrader.vue'
import StockHome from './pages/StockHome'
import StockPortfolio from './pages/StockPortfolio'
import StockPage from './pages/StockPage'
import SolomonPage from './pages/SolomonPage'

Vue.use(Router)

export default new Router({
  // mode: 'history',
  // base: process.env.BASE_URL,

  routes: [
    { path: '/', component: About, name: 'about' },
    { path: '/skills', component: Skills, name: 'skills' },
    { path: '/portfolio', component: Portfolio, name: 'portfolio' },
    { path: '/quotes', component: Quotes, name: 'quotes' },
    {
      path: '/stock',
      component: Stock,
      name: 'stock',
      children: [
        { path: '/stockHome', component: StockHome, name: 'stockHome' },
        { path: '/stockPortfolio', component: StockPortfolio, name: 'stockPortfolio' },
        { path: '/stockPage', component: StockPage, name: 'stockPage' },
        { path: '/solomonPage', component: SolomonPage, name: 'solomonPage' }
      ]
    },

    { path: '*', redirect: { name: 'about' } }
  ]

})
